import { Injectable, inject } from '@angular/core';
import { MessageService, ToastMessageOptions } from 'primeng/api';

@Injectable({
  providedIn: 'root',
})
export class ToastService {
  private readonly messageService = inject(MessageService);

  /**
   * Show a success toast message.
   * @param summary The summary of the message.
   * @param detail The detail of the message.
   * @param options Additional options for the toast message.
   */
  showSuccess(
    summary: string,
    detail: string,
    options?: Partial<ToastMessageOptions>,
  ): void {
    this.messageService.add({
      severity: 'success',
      summary,
      detail,
      ...options,
    });
  }

  /**
   * Show an error toast message.
   * @param summary The summary of the message.
   * @param detail The detail of the message.
   * @param options Additional options for the toast message.
   */
  showError(
    summary: string,
    detail: string,
    options?: Partial<ToastMessageOptions>,
  ): void {
    this.messageService.add({ severity: 'error', summary, detail, ...options });
  }

  /**
   * Show a warning toast message.
   * @param summary The summary of the message.
   * @param detail The detail of the message.
   * @param options Additional options for the toast message.
   */
  showWarning(
    summary: string,
    detail: string,
    options?: Partial<ToastMessageOptions>,
  ): void {
    this.messageService.add({ severity: 'warn', summary, detail, ...options });
  }

  /**
   * Show an info toast message.
   * @param summary The summary of the message.
   * @param detail The detail of the message.
   * @param options Additional options for the toast message.
   */
  showInfo(
    summary: string,
    detail: string,
    options?: Partial<ToastMessageOptions>,
  ): void {
    this.messageService.add({ severity: 'info', summary, detail, ...options });
  }
}
